.ModalFade {
    position: fixed;
    display: block;
    top: 0;
    left: 0;
    width : 100%;
    height : 100vh;
    background-color: rgba(0,0,0,0.95);
    z-index: 2;

    .ModalContent {
        z-index: 3;
        position: absolute;
        border-radius : 4px;
        width : 80%;
        min-height : 400px;
        max-height : 90vh;
        max-width : 900px;
        background-color: #101010;
        color: white;
        opacity: 1;
        left : 50%;
        top : 50%;
        transform: translate(-50%, -50%);
        box-sizing: border-box;
        display: flex;
        flex-direction: column; } }
